@media only screen and (min-width: 414px) and (max-width: 767px) {
  .main {
    margin: 0px 0px;
  }
}
@media only screen and (min-width: 768px) {
  .main {
    margin: 0px 150px;
  }
}

.list {
  list-style-type: none;
  padding: 0px;
}
.card {
  height: 120px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-template-areas: 
      "info score"
      "body body"
      "body body"
      "body body"
      "source date";
  border: 1px solid var(--text-color);
  margin: 4px auto;
  font-size: 0.8em;
  border-radius: 4px;
  max-width: 1000px;
}
.info {
  grid-area: info;
  margin: 0px 8px;
}
.source {
  grid-area: source;
  margin: 0px 8px;
}
.score {
  grid-area: score;
  text-align: right;
  margin: 0px 8px;
}
.date {
  grid-area: date;
  text-align: right;
  margin: 0px 8px;
}
.text {
  grid-area: body;
  margin: 0px 8px;
  text-align: center;
  padding: 30px 0px;
}
.selected {
  background-color: rgba(0, 255, 65, 0.5);
}
.wrapText {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}