@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400&display=swap);
html, body {
  --background-color: rgb(31, 31, 31);
  --text-color: rgb(223, 223, 223);
  margin: 0px;
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--text-color);
  background-color: var(--background-color);
}

@media only screen and (min-width: 414px) and (max-width: 767px) {
  .main {
    margin: 0px 0px;
  }
}
@media only screen and (min-width: 768px) {
  .main {
    margin: 0px 150px;
  }
}

.list {
  list-style-type: none;
  padding: 0px;
}
.card {
  height: 120px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-template-areas: 
      "info score"
      "body body"
      "body body"
      "body body"
      "source date";
  border: 1px solid var(--text-color);
  margin: 4px auto;
  font-size: 0.8em;
  border-radius: 4px;
  max-width: 1000px;
}
.info {
  grid-area: info;
  margin: 0px 8px;
}
.source {
  grid-area: source;
  margin: 0px 8px;
}
.score {
  grid-area: score;
  text-align: right;
  margin: 0px 8px;
}
.date {
  grid-area: date;
  text-align: right;
  margin: 0px 8px;
}
.text {
  grid-area: body;
  margin: 0px 8px;
  text-align: center;
  padding: 30px 0px;
}
.selected {
  background-color: rgba(0, 255, 65, 0.5);
}
.wrapText {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.Loader_loader__3MXRD {
    border: 16px solid rgb(243, 243, 243);
    border-top: 16px solid rgb(52, 152, 219);
    border-radius: 50%;
    width: 64px;
    height: 64px;
    -webkit-animation: Loader_spin__35Z7G 2s linear infinite;
            animation: Loader_spin__35Z7G 2s linear infinite;
    z-index: 1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
  
@-webkit-keyframes Loader_spin__35Z7G {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
  
@keyframes Loader_spin__35Z7G {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
